	module.exports = {"data":{"markdownRemark":{"html":"<p>The greatest adventure motorcycle available on the American market is the Suzuki DR650.\nEarlier this year I had the opportunity to purchase a 2007 Suzuki DR650 from one of the my oldest brother's close friends. He had stopped\nriding it as he had settled in to focus on his career as a Lineman, and had posted the machine for an extremely modest price. I was thrilled to hear\nand bought it immediately without any hesitation. The DR650 is the world's most badass bike - a dual-sport motorcycle with a counterbalanced, single cylinder, air/oil-cooled engine that outputs 46 horsepower from the factory.\nThe bike is 324 lbs dry, highway capable, a dirt ripper, and has incredible aftermarket. It one of the rtw (round the world) rider's most popular choices to tour - it is incredibly simple to maintain, can carry a massive amount of luggage,\nis more capable on rugged terrain than big BMW adventure bikes, and dead reliable.</p>\n<iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/spAq8JuXVUY\" frameborder=\"0\" gesture=\"media\" allow=\"encrypted-media\" allowfullscreen></iframe>","frontmatter":{"title":"The DR650"}}},"pathContext":{"slug":"/blog-posts/the-dr650/"}}

